import React, { Fragment,  useState} from "react"

const MfrSelection = props => {
  const { partyTable, setCurrentSU, setCurrentMF, setCurrentSUName, setCurrentMFName, selectingSupplier, setSelectingSupplier, setMFRsearch, mfrSearch, 
    mfrSearchRef, getMCtable, setEditMode } = props

  const [entryPosition, setEntryPosition] = useState(-1)


  const handleClick = (su, mf, su_name, mf_name) => {
    setCurrentSU(su)
    setCurrentMF(mf)
    setCurrentSUName(su_name)
    setCurrentMFName(mf_name)
    setSelectingSupplier(false)
    getMCtable(su, mf)
    setEditMode(false)
  }

  const handleMFRsearch = (event)=> {
    const { value } = event.target      
    setMFRsearch(value.toUpperCase())
  }

  const handleKeyPressed = (event)=> {
    
    if ( event.key === "ArrowDown" ) {  
      const partyLen = partyTable.filter(y => mfrSearch.length === 0 || "".concat(" ", y.SU_NAME, " ", y.SU, " ", y.MF, " ", y.MF_NAME).search(mfrSearch) > 0).length        
      if (entryPosition < partyLen)  
          setEntryPosition(x=>x+1)             
      else    
          setEntryPosition(partyLen -1)                    
    }

    if ( event.key === "ArrowUp" ) {
      if (entryPosition > -1)  
        setEntryPosition(x=>x-1)       
      else 
        setEntryPosition(0)           
    }

    if ( event.key === "Enter" ) {      
      const partyList = partyTable.filter(y => mfrSearch.length === 0 || "".concat(" ", y.SU_NAME, " ", y.SU, " ", y.MF, " ", y.MF_NAME).search(mfrSearch) > 0) 
      //console.log( "selecting",  partyList[entryPosition].MF_NAME ); // does fire      
      const x = partyList[entryPosition]      
      setEntryPosition(0)  
      handleClick(x.SU, x.MF, x.SU_NAME, x.MF_NAME)
  
        
    }    
  }

  return (
    // present the table
    <Fragment>
      {partyTable && selectingSupplier ? (
        <div>
          <div className="mfrSelection_header"> Select a manufacturer below:</div>
          <div className="mfrSelection_input"  >
            <input autoFocus  ref={mfrSearchRef} type="text" id="mfrSearch" name="mfrSearch" value={mfrSearch || ""} justify="left" onChange={handleMFRsearch} style={{ textTransform: "uppercase" }}                         
              onKeyDown={handleKeyPressed}
              placeholder="Type to search" 
           />
          </div>
        </div>
      ) : null}

      {partyTable && selectingSupplier
        ? partyTable
            .filter(y => mfrSearch.length === 0 || "".concat(" ", y.SU_NAME, " ", y.SU, " ", y.MF, " ", y.MF_NAME).search(mfrSearch) > 0)
            .map((x, index) => (
              <div
                className={"mfrSelection"}
                key={index}
                onClick={() => {
                  handleClick(x.SU, x.MF, x.SU_NAME, x.MF_NAME)
                }}              >  
                <div className={"mfrSelection_capitalize  mfrSelection_mflist " + (entryPosition===index?"mfrSelection_enable":null)}>
                  <div> &nbsp; {(x.SU_NAME).toLowerCase()} ({x.SU}) </div>
                  <div> {x.MF_NAME ? (x.MF_NAME).toLowerCase() : null} ({x.MF}) &nbsp; </div>
                  {/*<div> {index}, {entryPosition}</div> */}
                </div>  
              </div>
            ))
        : null}
    </Fragment>
  )
}
export default MfrSelection
