import React, { Fragment, useState, useEffect } from "react"

const UserProfile = props => {
  const { userProfile} = props
  const [photoURL, setPhotoURL] = useState("")
  
  useEffect(() => {
    setPhotoURL("/api/getPhoto/" + userProfile.email)              
  }, [userProfile.email])

 
  return (
    <Fragment>      
             <div className={"userProfile ".concat(userProfile.databaseSchema === 'dev' ? 'userProfile_dev_color' : 'userProfile_prod_color')}>
            <div className="userProfile_d userProfile_grid_d">
                  <img src={userProfile.databaseSchema === 'dev' ? "static/img/macys_red_icon.png":  "static/img/macys_blue_icon.png"} alt="" height="60"  />
            </div>
            <div className="userProfile_e userProfile_grid_e">                
                booking@mmgoverseas
            </div>    
            <div className="userProfile_b userProfile_grid_b ">            
                  {userProfile.userName}
            </div>  
            <div className="userProfile_c userProfile_grid_c" >                
            </div>        
          <div className="userProfile_a userProfile_grid_a"><img src={photoURL ? photoURL: "static/img/anonymous.jpg"} aria-hidden alt="..." width="60" height="60" /> </div>   
        </div>
    </Fragment>
  )
}

export default UserProfile
