import { Route, Routes, HashRouter as Router, Link } from "react-router-dom"
import React, { useState, useEffect } from "react"
import axios from "axios"
import AppBooking from "./AppBooking"
import Home from "./Home"
import UserProfile from "./components/UserProfile"
import { trackPromise } from "react-promise-tracker"

const logout = () => {
  localStorage.clear()
  window.location = "/logout"
}

const App = () => {
  const [userProfile, setUserProfile] = useState({})

  useEffect(() => {
    trackPromise(
      axios({
        url: "/api/getUserProfile",
        method: "POST",
        headers: {
          "Content-type": "application/json",
          api: "yes"
        }
        //data: JSON.stringify({})   seems like this line has to be removed to avoid error from Heroku as this line is passing empty parameter.
      })
        .then(response => response.data)
        .then(response => {
          setUserProfile(response["userProfile"])
        })
        .catch(error => {
          console.log(error)
        })
    )

  }, [])

  return (
    <div className="container">
      <div className="flex-row">
        <div className="flex-large">
          <UserProfile userProfile={userProfile} />
          <Router>
            <nav>
              <ul className="app_header">
                <li>
                  <Link to="/" className="link">
                    Home
                  </Link>
                </li>
                <li>
                  <Link to="/appBooking" className="link">
                    Booking
                  </Link>
                </li>
                <div className="link" 
                  onClick={() => {
                    alert("Sorry not ready yet")
                  }}>
                  Print Bookings                  
                </div>
                <button
                  className="fa fa-sign-out"
                  onClick={() => {
                    logout()
                  }}
                >
                  Logout
                </button>
                {/* <div className="userProfile_photo">{userProfile.email ? userProfile.email.includes("@macys.com") ? <img src={"/api/getPhoto"} aria-hidden alt="Searching for photo..." width="60" height="60" /> : <img src="static/img/anonymous.jpg" aria-hidden alt="No factory photo !!" width="60" height="60" /> : <div>Searching for photo...</div>} </div>*/}
              </ul>
            </nav>

            <Routes>
              <Route path="/" element={<Home env={userProfile.environment} />} />
              <Route path="/appBooking" element={<AppBooking />} />
            </Routes>
          </Router>
        </div>
      </div>
    </div>
  )
}

export default App
