import React, { Fragment } from "react"

const SupplierInfo = props => {
  const { currentSU, currentMF, currentSUName, currentMFName, setSelectingSupplier, setShowFull, setMFRsearch,  
    setEntryPosition, mfrSearchRef   
  } = props

  const changeSelectingSuppler = () => {
    setMFRsearch("")
    setSelectingSupplier(true)
  }

  return (
    // present the table
    <Fragment>
      {!!currentSU ? (
        <div className="supplierInfo">
          <button
            onClick={() => {
              setShowFull(false)
              setEntryPosition(-1)                
              changeSelectingSuppler()            
              mfrSearchRef.current && mfrSearchRef.current.focus()              
            }}
          >
            Select
          </button>
          <div className="supplierInfo_capitalize supplierInfoSU" id="supplierInfo_bar">
            {currentSUName.toLowerCase()} ({currentSU})
          </div>
          <div className="supplierInfo_capitalize supplierInfoMF">
            {currentMFName.toLowerCase()} ({currentMF})
          </div>
        </div>
      ) : null}
    </Fragment>
  )
}
export default SupplierInfo
